import countries from '../country'

export default {
    title: 'Customers',
    icon: 'mdi-account-box',
    filters:[ ],
    table: [
        {
            text: 'TP Account',
            align: 'start',
            sortable: false,
            value: 'tpc',
        },
        { text: 'Account name', value: 'name' },
        { text: 'Country', value: 'country' },
        { text: 'Affiliate', value: 'affiliate.name' },
        { text: 'Actions', value: 'actions', width: '100' }

    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'tpc',
                    bind: {
                        label: "TP Account",
                        prependIcon: 'mdi-card-account-details',
                        required: true,


                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account Name",
                        prependIcon: 'mdi-account-box',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
               
             
                {
                    input: 'autocomplete',
                    name: 'country',
                    bind: {
                        label: "Country",
                        prependIcon: "mdi-flag",
                        items: countries,
                        itemText: "name",
                        itemValue: "code",
                        required: true,
                         
                        hideAddNew: true,
                        
                        rules: [
                            v => !!v || 'Country is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        
                        
                   
                        prependIcon: "mdi-account-network-outline",
                     

                    }
                }
            ]
        }
    ]
}