export default  {
        title: 'Users',
        icon: 'mdi-account-multiple',
        table: [
            {
              text: 'Display Name',
              align: 'start',
              sortable: false,
              value: 'displayName',
            },
            { text: 'Username', value: 'username' },
            { text: 'Role', value: 'role' },
            { text: 'Business Unit', value: 'business.name' },
            { text: 'Department', value: 'department.name' },
            { text: 'Actions', value: 'actions', width: '100' } 
        ],
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'text',
                        name: 'displayName',
                        bind: {
                            label: "Display Name",
                            prependIcon: 'mdi-account',
                            required: true,
                            rules: [
                                v => !!v || 'Display Name is required',
                            ],
                        } 
                    },
                    {
                        input: 'text',
                        name: 'username',
                        bind: {
                            label: "Username",
                            autocomplete: 'username',
                            prependIcon: 'mdi-account-box',
                            required: true,
                            rules: [
                                v => !!v || 'Username is required',
                            ],
                        } 
                    },
                    {
                        input: 'password',
                        name: 'password',
                        bind: {
                            label: "Password",
                            prependIcon: 'mdi-lock',
                             
                            
                        } 
                    },
                    {
                        input: 'autocomplete',
                        name: 'role',
                        bind: {
                            label: "Role", 
                            collection:'role',
                            itemText: "name",
                            itemValue: "role",
                            prependIcon: "mdi-key",
                            required: true,
                            rules: [
                                v => !!v || 'Role is required',
                            ],
                        } 
                    }, 
                    {
                        input: 'autocomplete',
                        name: 'business',
                        bind: {
                            label: "Business Unit",
                            prependIcon: "mdi-domain",
                            collection: 'business',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'autocomplete',
                        name: 'department',
                        bind: {
                            label: "Department",
                            prependIcon: "mdi-briefcase-account-outline",
                            collection: 'department',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'switch',
                        name: 'active',
                        default: true,
                        bind: {
                            label: "Active" 
                        } 
                    }
                ]
            }
        ]
    }