// import countries from '../country'

export default {
    title: 'Deposits',
    icon: 'mdi-bank-transfer-in',
    filters:[
        {
            input: 'autocomplete',
            name: 'business',
            if: state => !(state.auth.user.business),
            bind: {
                label: "Business Unit",
                prependInnerIcon: "mdi-domain",
                collection: 'business',
                itemText: "name",
                itemValue: "_id",
                hideAddNew: true,
               
            }
        },
        {
            input: 'autocomplete',
            name: 'department',
            if: state => !(state.auth.user.department),
            bind: {
                label: "Department",
                prependInnerIcon: "mdi-briefcase-account-outline",
                collection: 'department',
                itemText: "name",
                itemValue: "_id",
                hideAddNew: true,
                
            }
        },
        {
            input: 'autocomplete',
            name: 'user',
            if: state => (state.auth.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
            bind: {
                label: "User",
                prependInnerIcon: "mdi-account",
                collection: 'user',
                itemText: "displayName",
                itemValue: "_id",
                hideAddNew: true,
                
            }
        },

    ],
    table: [
        {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
        },
        { text: 'Account name', value: 'customer.name' },
        { text: 'Amount', value: 'amount' },
        // { text: 'Email', value: 'email' },
        { text: 'Payment Method', value: 'payment_method.name' },
        { text: 'Affiliate', value: 'customer.affiliate.name' },
        { text: 'Business Unit', value: 'business.name' },
        { text: 'Department', value: 'department.name' },
        { text: 'Date', value: 'created_on' },
        { text: 'Created By', value: 'user.displayName' },
        { text: 'Actions', value: 'actions', width: '100' }

    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'autocomplete',
                    name: 'customer',
                    bind: {
                        label: "Account",
                        prependIcon: "mdi-credit-card-settings-outline",
                        collection: 'customer',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        searchFields: ['name', 'tpc'],

                        rules: [
                            v => !!v || 'Payment Method is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'amount',
                    bind: {
                        label: "Amount",
                        prependIcon: 'mdi-cash',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },
               
                {
                    input: 'autocomplete',
                    name: 'payment_method',
                    bind: {
                        label: "Payment Method",
                        prependIcon: "mdi-credit-card-settings-outline",
                        collection: 'payment_method',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Payment Method is required',
                        ],
                    }
                },
              
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },

                {
                    input: 'autocomplete',
                    name: 'business',
                    if: state => !(state.auth.user.business),
                    default: state => state.auth.user.business,
                    bind: {
                        label: "Business Unit",
                        prependIcon: "mdi-domain",
                        collection: 'business',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Business Unit is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'department',
                    if: state => !(state.auth.user.department),
                    default: state => state.auth.user.department,
                    bind: {
                        label: "Department",
                        prependIcon: "mdi-briefcase-account-outline",
                        collection: 'department',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Department is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                        label: "User",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'User is required',
                        ],
                    }
                },
                
               
            ]
        }
    ]
}