import countries from '../country'

export default {
    title: 'Sales',
    icon: 'mdi-printer-pos',
    filters:[
        {
            input: 'text',
            name: 'name',
            bind: {
                dense: true,
                outlined: true,
                label: "Account Name",
                prependInnerIcon: 'mdi-account-box',
            }
        },
        {
            input: 'text',
            name: 'tpc',
            bind: {
                dense: true,
                outlined: true,
                label: "ID",
                prependInnerIcon: 'mdi-card-account-details',
            }
        },
        {
            input: 'text',
            name: 'amount',
            bind: {
                dense: true,
                outlined: true,
                label: "Amount",
                prependInnerIcon: 'mdi-cash',
                type: 'number',
            }
        },

        {
            input: 'autocomplete',
            name: 'payment_method',
            bind: {
                dense: true,
                outlined: true,
                label: "Payment Method",
                prependInnerIcon: "mdi-credit-card-settings-outline",
                collection: 'payment_method',
                itemText: "name",
                itemValue: "_id",
                hideAddNew: true,
            }
        },
        {
            input: 'autocomplete',
            name: 'user',
            bind: {
                dense: true,
                outlined: true,
                label: "Agent",
                prependInnerIcon: "mdi-account",
                collection: 'user',
                itemText: "displayName",
                itemValue: "_id",
                hideAddNew: true,
            }

        },
   
        {
            input: 'date',
            name: 'created_on',
            bind: {
                dense: true,
                outlined: true,
                label: "Created Date",
                prependInnerIcon: 'mdi-calendar',
            }
        },
        {
            input: 'select',
            name: 'business_unit',
            bind: {
                dense: true,
                outlined: true,
                label: "Business Unit",
                prependInnerIcon: "mdi-flag",
                items: ['en', 'ar', 'label1'],
            }

        },
        {
            input: 'autocomplete',
            name: 'country',
            bind: {
                dense: true,
                outlined: true,
                label: "Country",
                prependInnerIcon: "mdi-flag",
                items: countries,
                itemText: "name",
                itemValue: "code",
                hideAddNew: true,
            }
        },
        {
            input: 'autocomplete',
            name: 'affiliate',
            bind: {
                dense: true,
                outlined: true,
                label: "Affiliate",
                collection: 'affiliate',
                itemText: "name",
                itemValue: "_id",
                prependInnerIcon: "mdi-account-network-outline",
                hideAddNew: true,
            }
        },
        {
            name: 'type',
            value: 'sale',
            forced: true
        }

    ],
    table: [
        {
            text: 'TP Account',
            align: 'start',
            sortable: false,
            value: 'tpc',
        },
        { text: 'Account name', value: 'name' },
        { text: 'Amount', value: 'amount' },
        { text: 'Affiliate', value: 'affiliate.name' },
        // { text: 'Email', value: 'email' },
        { text: 'Payment Method', value: 'payment_method.name' },
        { text: 'Created date', value: 'created_on' },
        
        { text: 'Created By', value: 'user.displayName' },
        { text: 'Business Unit', value: 'business_unit' },
        { text: 'Actions', value: 'actions', width: '100' }

    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account Name",
                        prependIcon: 'mdi-account-box',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'tpc',
                    bind: {
                        label: "ID",
                        prependIcon: 'mdi-card-account-details',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'amount',
                    bind: {
                        label: "Amount",
                        prependIcon: 'mdi-cash',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },
               
                {
                    input: 'autocomplete',
                    name: 'payment_method',
                    bind: {
                        label: "Payment Method",
                        prependIcon: "mdi-credit-card-settings-outline",
                        collection: 'payment_method',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Payment Method is required',
                        ],
                    }
                },
                // {
                //     input: 'autocomplete',
                //     name: 'user',
                //     bind: {
                //         label: "Assign to",
                //         prependIcon: "mdi-account",
                //         collection: 'user',
                //         itemText: "displayName",
                //         itemValue: "_id",
                //         required: true,
                //         
                //         
                //         rules: [
                //             v => !!v || 'Payment Method is required',
                //         ],
                //     }

                // },
                {
                    input: 'hidden',
                    name: 'user',
                    // default: JSON.parse(localStorage.getItem('user')).business_unit
                    default: state => state.auth.user._id

                },
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },
                {
                    input: 'hidden',
                    name: 'business_unit',
                    // default: JSON.parse(localStorage.getItem('user')).business_unit
                    default: state => state.auth.user.business_unit
                     
                },
                {
                    input: 'autocomplete',
                    name: 'country',
                    bind: {
                        label: "Country",
                        prependIcon: "mdi-flag",
                        items: countries,
                        itemText: "name",
                        itemValue: "code",
                        required: true,
                         
                        hideAddNew: true,
                        
                        rules: [
                            v => !!v || 'Country is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        
                        
                   
                        prependIcon: "mdi-account-network-outline",
                     

                    }
                }
            ]
        }
    ]
}