export default {
    title: 'Business',
    icon: 'mdi-domain',
    table: [
        {
            text: 'Name',
            align: 'start',
            sortable: false,
            value: 'name',
        },
        { text: 'Actions', value: 'actions', width: '100' }
    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                }
            ]
        }
    ]
}